export default {
  props: {
    value: {
      type: String,
      default: ''
    }
  },
  data () {
    return {
      content: ''
    }
  },
  models: {
    prop: 'value',
    event: 'change'
  },
  computed: {
    selectedContent: {
      get () {
        return this.content
      },
      set (newValue) {
        this.content = newValue
        this.emit('change', newValue)
      }
    }
  }
}



